import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { CambiaClaveComponent } from './cambiaClave/cambiaClave.component';
import { Ng2TableModule } from 'ng2-table/ng2-table';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { NgxPaginationModule } from 'ngx-pagination';
import { SharedModule } from '../../shared/shared.module';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';

const routes: Routes = [
  { path: 'cambiaClave', component: CambiaClaveComponent }
];

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule.forChild(routes),
    Ng2TableModule,
    NgxDatatableModule,
    NgxPaginationModule,
    CommonPopUpModule
  ],
  declarations: [CambiaClaveComponent],
  exports: [
        RouterModule
    ]
})
export class CambiaClaveModule { }
